@import url(https://fonts.googleapis.com/css?family=Open+Sans+Condensed:300,300i,700&display=swap&subset=vietnamese);
:root {
  font-size: 16px !important;
  box-sizing: border-box !important;
  font-family: 'Open Sans Condensed', sans-serif !important;
  line-height: 1.2;
}

body {
  font-size: 16px !important;
  box-sizing: border-box !important;
  font-family: 'Open Sans Condensed', sans-serif !important;
}

.ant-layout-header {
    height: auto !important;
}

.ant-menu-vertical .ant-menu-item {
    height: auto !important;
    overflow: auto;
}
.navbar {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    overflow: hidden;
    width: 0;
    transition: width 0.8s ease;
}

.navbar.active {
    width: 14rem;
}

.navbar-nav {
    height: 100%;
    display: flex;
    flex-direction: column;
}

.navbar-nav .nav-item {
    text-align: center;
    display: flex;
    align-items: center;
    width: 100%;
    padding:10px 10px !important;
}

.navbar-nav .nav-item:last-child{
    margin-top: auto;
}

